import React, { useState } from "react";
import "./index.scss";

// ui
import { CContainer, CModal, CModalBody } from "@coreui/react";
// images
import Img1 from "../../../assets/images/img1.webp";
import Img2 from "../../../assets/images/img1-1.webp";
import Img3 from "../../../assets/images/img1-2.webp";
import Img4 from "../../../assets/images/img1-3.webp";

const ProductDetail = (props) => {
  const [modal, setModal] = useState(false);
  const [imagePopup, setImagePopup] = useState(null);

  const toggle = (img) => {
    setImagePopup(img);
    setModal(!modal);
  };

  return (
    <div className="productDetail">
      <CContainer>
        <div className="content">
          <h2>2916 Waverly Dr</h2>

          <div className="viewImage" onClick={() => toggle(Img1)}>
            <img src={Img1} alt="" />
          </div>

          <div className="viewImage" onClick={() => toggle(Img2)}>
            <img src={Img2} alt="" />
          </div>

          <div className="viewImage" onClick={() => toggle(Img3)}>
            <img src={Img3} alt="" />
          </div>

          <div className="viewImage" onClick={() => toggle(Img4)}>
            <img src={Img4} alt="" />
          </div>
        </div>

        <CModal centered={true} className="modalCustom" show={modal} onClose={toggle}>
          <CModalBody>
            <div className="contentCustom">
              <img src={imagePopup} alt="" />
            </div>
          </CModalBody>
        </CModal>
      </CContainer>
    </div>
  );
};

export default ProductDetail;
